
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {time, resetPayPwd} from '../config'
import {mapMutations} from 'vuex'
export default {
  name: 'passwordPay',
  head: {
    title: {
      inner: ''
    }
  },
  data () {
    return {
      show: false,
      loading: false,
      getting: false,
      text: '',
      start: false,
      time: time,
      payPwd: '',
      smsType: 1,
      form: {
        tel: '',
        userId: '',
        payPwd: '',
        captcha: ''
      }
    }
  },
  created () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
  },
  components: {
    Group,
    XInput,
    XButton,
    Countdown
  },
  methods: {
    handleSubmit () {
      if (!this.form.payPwd || !this.payPwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '',
          time: '1000'
        })
      } else if (this.form.payPwd !== this.payPwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '',
          time: '1000'
        })
      } else if (!this.form.captcha) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '',
          time: '1000'
        })
      } else if (!parseInt(this.form.payPwd)) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '6',
          time: '1000'
        })
      } else {
        this.$http({
          method: 'jsonp',
          url: resetPayPwd,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: this.form,
          before: () => {
            this.loading = true
          }
        })
        .then(res => {
          this.loading = false
          if (res.body.status) {
            this.$vux.toast.show({
              type: 'text',
              width: '15em',
              position: 'bottom',
              text: '',
              time: '1000'
            })
            let userInfo = JSON.parse(this.$localStorage.get('userInfo'))
            userInfo.hadPayPwd = true
            this.$localStorage.set('userInfo', JSON.stringify(userInfo))
            setTimeout(() => {
              this.$router.replace('/personer')
            }, 1000)
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '15em',
              position: 'bottom',
              text: res.body.msg,
              time: '1000'
            })
          }
        })
      }
    },
    handleTips () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: this.time + '',
        time: '1000'
      })
    },
    handleTime () {
      this.show = false
      this.start = false
      this.time = time
    },
    handleSendSms () {
      this.postSMS(this)
    },
    ...mapMutations({
      postSMS: 'postSMS'
    })
  }
}



